define('taoQtiItem/qtiCreator/widgets/static/helpers/image',[
    'jquery',
    'lodash',
    'i18n',
    'ui/dialog/confirm',
    'util/image'
], function ($, _, __, confirmDialog, imageUtil) {
    'use strict';

    /**
     * Extract a default label from a file/path name
     * @param {String} fileName - the file/path
     * @returns {String} a label
     */
    function extractLabel(fileName) {
        return fileName
            .replace(/\.[^.]+$/, '')
            .replace(/^(.*)\//, '')
            .replace(/\W/, ' ')
            .substr(0, 255);
    }

    return {
        /**
         * Set an image onto a widget
         * @param widget
         * @param file
         * @param alt
         */
        setImage: function setImage(widget, file, alt) {
            var $form = widget.$form;
            var options = widget.options;
            var img = widget.element;
            var $container = widget.$container;
            var $src = $form.find('input[name=src]');
            var $alt = $form.find('input[name=alt]');
            var w, maxW;
console.log('bouarf!')
            imageUtil.getSize(options.baseUrl + file, function (size) {

                if (size && size.width >= 0) {

                    w = parseInt(size.width, 10);
                    maxW = $container.parents().innerWidth();

                    // always set the image size in % of the container size with a security margin of 5%
                    if (w >= maxW * 0.95) {
                        w = 100;
                    } else {
                        w = parseInt(100 * w / maxW, 10);
                    }
                    img.attr('width', w + '%');
                    img.removeAttr('height');
                }

                if ($.trim($alt.val()) === '') {
                    if (alt === '') {
                        alt = extractLabel(file);
                    }
                    img.attr('alt', alt);
                    $alt.val(alt).trigger('change');
                } else {
                    confirmDialog(__('"%s"<br>with<br>"%s" ?', $alt.val(), alt), function () {
                        img.attr('alt', alt);
                        $alt.val(alt).trigger('change');
                    });
                }

                _.defer(function () {
                    $src.val(file).trigger('change');
                });
            });
        }
    };
});

